/* stylelint-disable */
@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto-Italic";
  src: url("../fonts/../fonts/Roboto-Italic.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
*, *::after, *::before {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", Arial, sans-serif;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.title {
  font-size: 50px;
  color: #fff;
  margin-bottom: 30px;
  margin-top: 0;
}

.description {
  font-size: 14px;
  color: #fff;
  margin-bottom: 20px;
  max-width: 80%;
  opacity: 0.7;
}

.auth-form {
  width: 400px;
  padding: 20px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.form__wrapper {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(41, 46, 73);
  background: linear-gradient(180deg, rgb(41, 46, 73) 50%, rgb(83, 105, 118) 100%);
}
.form__logo {
  position: absolute;
  left: 50%;
  top: 40px;
  transform: translateX(-50%);
}
.form__title {
  margin-top: 0;
  font-size: 30px;
  color: #fff;
  margin-bottom: 15px;
  opacity: 0.9;
  text-align: center;
}
.form-group {
  display: flex;
  align-items: center;
  position: relative;
}
.form-group:not(:last-of-type) {
  margin-bottom: 15px;
}
.form-group svg {
  margin-left: 5px;
  width: 25px;
  height: 25px;
  color: #fff;
  opacity: 0.7;
}
.form-group.btn-group {
  padding-top: 20px;
  justify-content: center;
}
.form-control {
  border: 0;
  background: none;
  outline: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  width: 100%;
  padding: 10px;
  padding-left: 0;
  color: #fff;
  font-size: 14px;
  margin-left: 8px;
}
.form-control::placeholder {
  font-size: 0;
}
.form-control:not(:placeholder-shown) + .form-label {
  top: -8px;
  font-size: 11px;
}
.form-label {
  font-size: 14px;
  color: #fff;
  position: absolute;
  left: 35px;
  top: 10px;
  opacity: 0.7;
  transition: all 150ms linear;
}

.btn {
  font-size: 12px;
  font-weight: 400;
  padding: 10px 15px;
  outline: none;
  border: none;
  cursor: pointer;
}
.btn-primary {
  position: relative;
  z-index: 1;
  color: #fff;
  background-color: transparent;
  transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
}
.btn-primary::before, .btn-primary::after {
  content: "";
  position: absolute;
  transition: inherit;
  left: 0;
  z-index: -1;
  height: 0;
  width: 100%;
}
.btn-primary::before {
  bottom: 0;
  border: 1px solid #fff;
  border-top: 0;
  border-bottom: 0;
}
.btn-primary::after {
  top: 0;
  height: 0;
  border-right: 1px solid #fff;
}
.btn-primary:hover, .btn-primary:focus {
  color: #23293C;
  transition-delay: 0.5s;
}
.btn-primary:hover::before, .btn-primary:hover::after, .btn-primary:focus::before, .btn-primary:focus::after {
  height: 100%;
}
.btn-primary:hover::after, .btn-primary:focus::after {
  background-color: #fff;
  transition-delay: 0.25s;
}
.btn-primary:hover::before, .btn-primary:focus::before {
  transition-delay: 0s;
}
.btn-outline {
  font-size: 12px;
  color: #fff;
  padding: 10px 20px;
  background-color: transparent;
  opacity: 0.7;
  border: none;
  position: relative;
  transition: all 0.3s;
}
.btn-outline::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transform: scale(0.1, 1);
}
.btn-outline::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s;
  background-color: rgba(255, 255, 255, 0.1);
}
.btn-outline:hover {
  letter-spacing: 2px;
  opacity: 1;
}
.btn-outline:hover::before {
  opacity: 1;
  transform: scale(1, 1);
}
.btn-outline:hover::after {
  opacity: 0;
  transform: scale(0.1, 1);
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
}
.header__row {
  padding-top: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}
.header__link {
  font-size: 14px;
}
.header_logo {
  position: relative;
  z-index: 2;
}

.sales-label {
  text-align: center;
  width: 100%;
  padding-top: 10px;
  color: #fff;
}

.index-video {
  position: relative;
  z-index: 1;
  height: 100vh;
  width: 100%;
  background-color: #23293C;
}
.index-video video {
  width: 100%;
  height: 100%;
}
.index-video::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: #000;
  position: absolute;
  inset: 0;
  z-index: 1;
  opacity: 0.7;
}

.stock {
  background-color: #23293C;
  padding: 45px 0;
}
.stock__row {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.stock__content {
  color: #fff;
  margin-bottom: 10px;
  font-size: 18px;
  text-align: center;
}
.stock__content i {
  font-family: "Roboto-Italic", "Roboto", sans-serif;
  font-weight: 700;
  background: #83da51; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #83da51, #00d0ff); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #83da51, #00d0ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.stock__countdown {
  display: flex;
  align-items: center;
  gap: 10px;
}
.stock__countdown .count {
  display: flex;
  align-items: center;
}
.stock__countdown .count span {
  display: block;
  width: 36px;
  padding: 5px 0;
  font-size: 32px;
  text-align: center;
  color: #fff;
}
.stock__countdown .val {
  margin-top: 5px;
  color: #fff;
  font-weight: 700;
}
.stock-countdown__value {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.preview__row {
  display: flex;
}
.preview__block {
  position: relative;
  width: 50%;
  height: 600px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.preview__block::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  inset: 0;
}
.preview__content {
  position: relative;
  z-index: 1;
  margin-bottom: 60px;
}
.preview__container {
  padding-left: 35px;
  height: 100%;
  display: flex;
  align-items: flex-end;
}

.catalog-section {
  padding: 50px 0;
  background-color: #23293C;
}
.catalog-section__row {
  display: flex;
  margin: 0 -10px;
}
.catalog-section__col {
  padding: 0 10px;
  height: 500px;
  width: 50%;
}
.catalog-section__link {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding: 35px 40px;
  position: relative;
}
.catalog-section__link::after {
  content: "";
  display: block;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  z-index: 0;
  inset: 0;
}
.catalog-section__link:hover > .catalog-section__name::after, .catalog-section__link:hover > .catalog-section__description::after {
  width: 100%;
}
.catalog-section__name, .catalog-section__description {
  display: block;
  width: fit-content;
  color: #fff;
  position: relative;
  z-index: 1;
}
.catalog-section__name::after, .catalog-section__description::after {
  content: "";
  display: block;
  width: 0;
  background-color: #fff;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: 0.3s linear;
}
.catalog-section__name {
  font-size: 25px;
  margin-bottom: 25px;
}
.catalog-section__description {
  font-size: 16px;
  margin-left: 10px;
}
.catalog-section__description::after {
  transition-delay: 0.2s;
}

.beast-seller {
  background-color: #23293C;
  height: 600px;
}
.beast-seller__row {
  height: 100%;
  display: flex;
  align-items: flex-end;
}
.beast-seller__col:first-child {
  width: 70%;
}

.product-card {
  width: 25%;
}