.btn {
    font-size: 12px;
    font-weight: 400;
    padding: 10px 15px;
    outline: none;
    border: none;

    cursor: pointer;

    &-primary {
        position: relative;
        z-index: 1;

        color: $white;
        background-color: transparent;

        transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);

        &::before, &::after {
            content: '';
            position: absolute;	
            transition: inherit;
            left: 0;
            z-index: -1;
            height: 0;
            width: 100%;
        }

        &::before {
            bottom: 0;	
            border: 1px solid $white;
            border-top: 0;
            border-bottom: 0;
        }

        &::after {
            top: 0;
	        height: 0;
            border-right: 1px solid $white;
        }

        &:hover,
        &:focus {
            color: $dark-blue;
            transition-delay: .5s;

            &::before, &::after {
                height: 100%;
            }

            &::after {
                background-color: $white;
                transition-delay: .25s;
            }

            &::before {
                transition-delay: 0s;
            }
        }
    }

    &-outline {
        font-size: 12px;
        color: $white;
        padding: 10px 20px;
        background-color: transparent;
        opacity: .7;
        border: none;
        
        position: relative;

        transition: all .3s;

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            opacity: 0;
            transition: all 0.3s;
            border-top: 1px solid rgba(255,255,255,0.5);
            border-bottom: 1px solid rgba(255,255,255,0.5);
            transform: scale(0.1, 1);
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            transition: all 0.3s;
            background-color: rgba(255,255,255,0.1);
        }

        &:hover {
            letter-spacing: 2px;
            opacity: 1;

            &::before {
                opacity: 1; 
                transform: scale(1, 1);
            }

            &::after {
                opacity: 0; 
                transform: scale(0.1, 1);
            }
        }
    }
}