.auth-form {
    width: 400px;
    padding: 20px;
    border-radius: 20px;
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15px );
    -webkit-backdrop-filter: blur( 15px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.form {
    &__wrapper {
        position: relative;
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgb(41,46,73);
        background: linear-gradient(180deg, rgba(41,46,73,1) 50%, rgba(83,105,118,1) 100%);
    }

    &__logo {
        position: absolute;
        left: 50%;
        top: 40px;
        transform: translateX(-50%);
    }

    &__title {
        margin-top: 0;
        font-size: 30px;
        color: $white;
        margin-bottom: 15px;
        opacity: .9;
        text-align: center;
    }

    &-group {
        display: flex;
        align-items: center;
        position: relative;

        &:not(:last-of-type) {
            margin-bottom: 15px;
        }

        svg {
            margin-left: 5px;
            width: 25px;
            height: 25px;
            color: $white;
            opacity: .7;
        }

        &.btn-group {
            padding-top: 20px;
            justify-content: center;
        }
    }

    &-control {
        border: 0;
        background: none;
        outline: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);

        width: 100%;
        padding: 10px;
        padding-left: 0;
        color: $white;
        font-size: 14px;
        margin-left: 8px;

        &::placeholder {
            font-size: 0;
        }

        &:not(:placeholder-shown) + .form-label {
            top: -8px;
            font-size: 11px;
        }
    }

    &-label {
        font-size: 14px;
        color: $white;
        position: absolute;
        left: 35px;
        top: 10px;
        opacity: .7;

        transition: all 150ms linear;
    }
}