.stock {
    background-color: $dark-blue;
    padding: 45px 0;
    &__row {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__content {
        color: $white;
        margin-bottom: 10px;
        font-size: 18px;
        text-align: center;

        i {
            font-family: "Roboto-Italic", "Roboto", sans-serif;
            font-weight: 700;
            background: #83da51;  /* fallback for old browsers */
            background: -webkit-linear-gradient(to right, #83da51, #00d0ff);  /* Chrome 10-25, Safari 5.1-6 */
            background: linear-gradient(to right, #83da51, #00d0ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
            
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }

    &__countdown {
        display: flex;
        align-items: center;
        gap: 10px;

        .count {
            display: flex;
            align-items: center;
    
            span {
                display: block;
                width: 36px;
                padding: 5px 0;
                font-size: 32px;
                text-align: center;
                color: $white;
            }
        }
    
        .val {
            margin-top: 5px;
            color: $white;
            font-weight: 700;
        }
    }

    &-countdown {
        &__value {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}