.catalog-section {
    padding: 50px 0;
    background-color: $dark-blue;

    &__row {
        display: flex;
        margin: 0 -10px;
    }

    &__col {
        padding: 0 10px;
        height: 500px;
        width: 50%;
    }

    &__link {
        display: block;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        padding: 35px 40px;

        position: relative;

        &::after {
            content: '';
            display: block;
            background-color: rgba(#000, .4);
            position: absolute;
            z-index: 0;
            inset: 0;
        }

        &:hover {
            & > .catalog-section__name::after,
            & > .catalog-section__description::after {
                width: 100%;
            }
        }
    }

    &__name,
    &__description {
        display: block;
        width: fit-content;
        color: $white;
        position: relative;
        z-index: 1;

        &::after {
            content: "";
            display: block;
            width: 0;
            background-color: $white;
            height: 1px;

            position: absolute;
            bottom: 0;
            left: 0;
            transition: .3s linear;
        }
    }

    &__name {
        font-size: 25px;
        margin-bottom: 25px;
    }

    &__description {
        font-size: 16px;
        margin-left: 10px;

        &::after {
            transition-delay: .2s;
        }
    }
}