.header {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;

    &__row {
        padding-top: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: $white;
    }

    &__link {
        font-size: 14px;
    }

    &_logo {
        position: relative;
        z-index: 2;
    }
}

.sales-label {
    text-align: center;
    width: 100%;
    padding-top: 10px;
    color: $white;
}