.preview {
    &__row {
        display: flex;
    }

    &__block {
        position: relative;
        width: 50%;
        height: 600px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        &::after {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(#000, .6);
            position: absolute;
            inset: 0;
        }
    }

    &__content {
        position: relative;
        z-index: 1;
        margin-bottom: 60px;
    }

    &__container {
        padding-left: 35px;
        height: 100%;
        display: flex;
        align-items: flex-end;
    }
}