*, *::after, *::before {
    box-sizing: border-box;
}

body {
    font-family: "Roboto", Arial, sans-serif;
    overflow-x: hidden;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.title {
    font-size: 50px;
    color: $white;
    margin-bottom: 30px;
    margin-top: 0;
}

.description {
    font-size: 14px;
    color: $white;
    margin-bottom: 20px;
    max-width: 80%;
    opacity: .7;
}