.index-video {
    position: relative;
    z-index: 1;
    height: 100vh;
    width: 100%;
    background-color: $dark-blue;

    video {
        width: 100%;
        height: 100%;
    }

    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background: #000;
        position: absolute;
        inset: 0;
        z-index: 1;
        opacity: .7;
    }
}